.hero-img {
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    height: 60vh;
    position: relative;
}

.hero-img::before {
    content: "";
    background: url("https://plus.unsplash.com/premium_photo-1663040543387-cb7c78c4f012?q=80&w=1170&auto=format&fit=crop&ixlib=rb-4.1.0&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D");
    background-size: cover;
    background-position: center top;
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

.heading {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.hero-img h1 {
    font-size: 2,4rem;
}

.hero-img p {
    font-size: 1rem;
    text-align: center;
    max-width: 1000px;
    margin: 0 auto;
   padding: 0 10px;
}

.cv-button {
    display: inline-block;
    padding: 12px 24px;
    background-color: #00bcd4;
    color: white;
    text-decoration: none;
    font-weight: 600;
    border-radius: 6px;
    transition: 0.3s ease;
    margin-top: 24px;
}

.cv-button:hover {
    background-color: hsl(187, 44%, 86%);
    transform: translateY(-2px);
}

@media screen and (max-width: 640px) {
    .hero-img h1 {
    font-size: 2rem;
}
}